<template>
  <section>
    <el-row>
      <el-col :span="8">
        <el-input v-model="search.usedPhrasesName" suffix-icon="el-icon-search"
        @keyup.enter.native="getData()" clearable
        @blur="getDataBySearch()"
         placeholder="输入您想查询的模板名称"></el-input>
      </el-col>
      <el-col :span="15" align="right">
        <el-button @click="handelAddItem"
         size="small" type="primary" icon="el-icon-plus">新 增</el-button>
      </el-col>
      <el-col :span="24" style="margin: 16px 0 0 8px">
        <el-radio v-model="usedphrasestype" @change="handelRadioChange" label="">全部</el-radio>
        <el-radio v-model="usedphrasestype" @change="handelRadioChange" label="40">主诉</el-radio>
        <el-radio v-model="usedphrasestype" @change="handelRadioChange" label="20">过敏史</el-radio>
        <el-radio v-model="usedphrasestype" @change="handelRadioChange" label="30">既往史</el-radio>
        <el-radio v-model="usedphrasestype" @change="handelRadioChange" label="10">现病史</el-radio>
        <el-radio v-model="usedphrasestype" @change="handelRadioChange" label="50">体格检查</el-radio>
        <el-radio v-model="usedphrasestype" @change="handelRadioChange" label="60">其他</el-radio>
        <el-radio v-model="usedphrasestype" @change="handelRadioChange" label="70">医嘱</el-radio>
      </el-col>
      <el-col :span="24" style="margin-top: 16px">
        <el-table :data="tableData"
        @cell-dblclick="handelView"
         style="width: 100%;">
          <el-table-column :formatter="elFormatMuBanName"
            prop="usedphrasestype"
            label="模板类型"
            width="100">
          </el-table-column>
          <el-table-column
            prop="usedphrasesname"
            label="模板名称"
            width="160">
          </el-table-column>
          <el-table-column
            prop="pinyincode"
            label="拼音码"
            width="120">
          </el-table-column>
          <el-table-column show-overflow-tooltip
            prop="usedphrasescontent"
            label="模板内容">
          </el-table-column>
          <el-table-column
            width="160"
            prop="remarksub"
            label="备注">
          </el-table-column>
          <el-table-column header-align="center" width="120"
            label="操作">
            <template slot-scope="scope">
              <el-button-group>
                <span class="icon-button-img" @click="handelDelete(scope.row)" ><img src="../../../assets/imgs/delete.png"></span>
                <span class="icon-button-img" @click="handelUpdata(scope.row)"><img src="../../../assets/imgs/edit.png"></span>
              </el-button-group>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
      <el-col :span="24" class="toolbar">
        <el-pagination background layout="total, prev, pager, next, jumper" @current-change="handelPageChange" :current-page="search.pageNumber" :page-size="search.pageSize" :total="total" style="float:right;">
        </el-pagination>
      </el-col>
    </el-row>
    <el-dialog :title="'模板设置 ' + title" center
    width="80%"
    @close="dialogClose"
    :visible.sync="dialog_item">
      <el-row>
        <el-form :model="formData" label-width="100px"
        :rules="rules" ref="ruleForm">
        <el-col :span="8">
          <el-form-item label="模板名称" prop="usedphrasesname2">
            <el-input v-model="formData.usedphrasesname2"
            @blur="funPinYinCode"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="拼音码" prop="pinyincode">
            <el-input v-model="formData.pinyincode"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="模板类型" prop="usedphrasestype2">
            <el-select v-model="formData.usedphrasestype2" placeholder="请选择">
              <el-option
                v-for="item in mubanList"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="2" style="text-align: right;max-width: 100px; padding-right: 6px">
          所属科室
        </el-col>
        <el-col :span="22" class="dept-box">
          <el-checkbox-group v-model="deptList">
              <el-checkbox v-for="item in keshi" :key="item.deptCode" :label="item.deptName"></el-checkbox>
          </el-checkbox-group>
        </el-col>
        <el-col :span="24">
          <el-form-item label="模板内容" prop="usedphrasescontent">
            <el-input type="textarea" :rows="2"
            placeholder="请输入内容" v-model="formData.usedphrasescontent"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-form-item label="备注">
            <el-input type="textarea" :rows="2"
            placeholder="请输入内容" v-model="formData.remarksub"></el-input>
          </el-form-item>
        </el-col>
        </el-form>
        <el-col class="toolbar" align='center'>
          <el-button size="small" @click="dialog_item=false">取消</el-button>
          <el-button type="primary" size="small" v-if="buttonShow" @click="handelSubmit()">确定</el-button>
        </el-col>
      </el-row>
    </el-dialog>
  </section>
</template>
<script>
import {
  deleteUsedPhrases, // 删除模板
  getUsedPhrasesList, // 获取模板列表
  insertUsedPhrases, // 添加模板
  updateUsedPhrases, // 更新模板
} from '../../../api'
import {mapState} from 'vuex'
import {convertPinyin} from '../../../util/getPinYin'
export default {
  computed:{
    ...mapState('common',{
      keshi: state => state.keshi,
    }),
    usable:function (){
      if(this.opType=='add'){
        return false
      }else{
        return true
      }
    }
  },
  data(){
    return{
      tableData: [], //表格数据
      usedphrasestype: '', // 模板选择
      dialog_item: false, // 弹窗显示
      opType: 'add',
      formData: {},
      total: 0, // 分页 总条数
      title: '',
      options: [], // 测试数据
      buttonShow: false, // 确定是否显示
      saveData:{ // 存放数据
        usedphrasesname: '',
        usedphrasestype: ''
      },
      
      search:{
        searchText: '',
        pageNumber: 1,
        pageSize: 10,
      },
      deptList:[] , // 科室列表
      rules:{
        usedphrasesname2: [{ required: true, message: '请输入模板名称', trigger: 'blur' }],
        pinyincode: [{ required: true, message: '请输入输入码', trigger: 'blur' }],
        usedphrasestype2: [{ required: true, message: '请输入模板类型', trigger: 'change' }],
        usedphrasescontent: [{ required: true, message: '请输入模板内容', trigger: 'blur' }],
      },
      mubanList: [
        {
          value: 40,
          label: '主诉'
        },
        {
          value: 20,
          label: '过敏史'
        },
        {
          value: 30,
          label: '既往史'
        },
        {
          value: 10,
          label: '现病史'
        },
        {
          value: 50,
          label: '体格检查'
        },
        {
          value: 60,
          label: '其他'
        },
        {
          value: 70,
          label: '医嘱'
        },
        {
          value: 80,
          label: '全部'
        },
      ],
    }
  },
  methods:{
    handelSubmit(){ // 确定操作
      if(this.opType=='add'){
        this.funChenkFormData()
      }else{
        this.handelUpdataDW()
      }
    },
    funPinYinCode(){ // 转换拼音码 
      if(this.formData.usedphrasesname2 && this.opType=='add'){
        this.formData.pinyincode = convertPinyin(this.formData.usedphrasesname2)
      }
    },
    handelRadioChange(value){ // 单选选择操作
      this.usedphrasestype = value
      this.search.pageNumber = 1
      this.getData()
    },
    funChenkFormData(){ // 检验提交数据
      if(this.deptList.length == 0){
          this.$message.warning('请选择所属科室！')
          return
        }
      this.$refs.ruleForm.validate((valid) => {
        if(valid){
          this.handelInsert()
        }
      })
    },
    handelInsert(){ // 插入数据
      this.formData.usedphrasesname = this.formData.usedphrasesname2
      this.formData.usedphrasestype = this.formData.usedphrasestype2
      this.formData.deptcode = this.funFormart()
      insertUsedPhrases(this.formData).then(res=>{
        if(res.data.code=='0'){
          this.$message.success('保存成功！')
          this.dialog_item = false
          this.funGetAllData()
        }else{
          this.$message.error('保存失败！')
        }
      })
    },
    handelAddItem(){ // 添加
      this.title = '新增'
      this.deptList = []
      this.opType = 'add'
      this.formData = {
        usedphrasesname2: '',
        pinyincode: '',
        usedphrasestype2: '',
        usedphrasescontent: '',
      }
      this.dialog_item = true
      this.buttonShow = true
    },
    handelDelete(row,scope){ // 列表项删除
      this.$confirm('确定要执行删除操作？','操作提示',{
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(()=>{ // 删除确定
        deleteUsedPhrases({usedphrasestype: row.usedphrasestype,
          usedphrasesname: row.usedphrasesname}).then(res=>{
          if(res.data.code == '0'){
            this.$message.success("删除成功!")
          }
        })
        this.funGetAllData()
        //this.tableData.splice(scope.$index,1)
      }).catch(()=>{ // 删除取消
        
      })
    },
    funSaveTempData(data){ // 保存临时数据
      this.saveData.usedphrasesname = data.usedphrasesname
      this.formData.usedphrasesname2 = data.usedphrasesname
      this.saveData.usedphrasestype = data.usedphrasestype
      this.formData.usedphrasestype2 = data.usedphrasestype
    },
    funGetAllData(){ // 查询所有数据
      this.search.searchText = ''
      this.search.pageNumber = 1
      this.getData()
    },
    handelView(row){ // 查看详情
      this.title = '查看'
      this.opType='view'
      this.formData = {...row}
      this.funSaveTempData(this.formData)
      this.funFill(row.deptcode)
      this.dialog_item = true
    },
    funFill(codes){ // 填充选择列表
      let list = codes.split(',')
      list.forEach(element => {
        this.keshi.forEach(item=>{
          if(element == item.deptCode){
            this.deptList.push(item.deptName)
          }
        })
      })
    },
    funFormart(){ // 序列化code 
      let codes = ''
      this.deptList.forEach(element=>{
        this.keshi.forEach(item=>{
          if(item.deptName == element){
            codes = codes +  item.deptCode + ','
          }
        })
      })
      return codes
    },
    handelUpdata(row){ // 修改按钮
      this.title = '修改'
      this.opType='edit'
      this.deptList = []
      this.funFill(row.deptcode)
      this.buttonShow = true
      this.dialog_item = true
      this.formData = row
      this.funSaveTempData(row)
    },
    dialogClose(){ // 对话框关闭回调
      this.buttonShow = false
      this.formData = {} //  移除表单数据
      this.$nextTick(()=>{
        this.$refs.ruleForm.resetFields();
      })
    },
    getData(){ // 获取列表数据
      let obj = {usedPhrasesType: this.usedphrasestype,...this.search}
      getUsedPhrasesList(obj).then(res=>{
        if(res.data.code=="0"){
          this.tableData = res.data.data.rows
          this.total = res.data.data.total
        }else{
          this.tableData = []
          this.total = 0
        }
      })
    },
    getDataBySearch(){ // 搜索框查询
      if(this.search.usedPhrasesName){
        this.search.pageNumber = 1
        this.getData()
      }
    },
    handelPageChange(page){ // 翻页
      this.search.pageNumber = page
      this.getData()
    },
    handelUpdataDW(){ // 更新数据
      this.formData.deptcode = this.funFormart()
      updateUsedPhrases({...this.formData,...this.saveData}).then(res=>{
        if(res.data.code=='0'){
          this.$message.success('修改成功!')
          this.getData()
          this.dialog_item = false
        }else{
          this.$message.error('修改失败!')
        }
      })
    }
  },
  mounted(){
    this.getData() // 列表初始数据
  }
}
</script>
<style scoped>
.el-checkbox {
    margin: 0 10px !important;
    height: 24px;
}
.dept-box{
  max-height: 100px;
  margin-bottom: 12px;
  overflow-y: scroll;
}
</style>


